import React from "react";
import { Typography } from "antd";

const InfoItem = ({ info, style }) => {
  return (
    <div className="anime__block" style={style}>
      <Typography.Title level={5} className="anime__block-title">
        {info.title}
      </Typography.Title>
      {info.list.map(
        (item, i) =>
          item &&
          item[1] && (
            <p className="anime__block-item" key={i}>
              {item[0] ? <span className="title">{item[0]}: </span> : null}
              <span className="text">{item[1]}</span>
            </p>
          )
      )}
    </div>
  );
};

export default InfoItem;
