import React, { useEffect } from "react";
import { Row, Col, Card, Typography, Skeleton } from "antd";

import { useLazyGetNewsQuery } from "../../services/newsApi";
import './style.css'

const News = ({ short = false, query = "anime", loading = false }) => {
  const [getNews, { data, isFetching }] = useLazyGetNewsQuery("1");

  const newsSlice = short ? [0,6] : [0];
  console.log(newsSlice)

  useEffect(() => {
    if (!loading) getNews(query);
  }, []);

  return (
    <Row gutter={[3, 3]} className={`news ${short ? '' : 'container'}`}>
      {(!data || isFetching) && "Loading news..."}
      {data &&
        !isFetching &&
        data.news.slice(...newsSlice).map(
          (item, i) =>
             (
              <Col className="news-item">
                <a href={item.url} target="_blank" rel="noreferrer">
                  <Card className="news-card" hoverable>
                    <Typography.Title className="news-title" level={5}>
                      {item.title} {console.log(window.xx = data.news)}
                    </Typography.Title>
                    
                    <p className="news-info">
                      <img
                        loading="lazy"
                        src={item.image}
                        onError={(e) => {
                          if (e.target.dataset.reloaded) return;
                          e.target.dataset.reloaded = true;
                          e.target.src = e.target.src + " ";
                        }}
                      />{" "}
                      {item.body}
                    </p>
                    <p className="news-end">
                      {item.date} <br />
                      {item.source}
                    </p>
                  </Card>
                </a>
              </Col>
            )
        )}
    </Row>
  );
};

export default News;
